.remote-access-app {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #333;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.app-header {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.app-header h2 {
  margin: 0;
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
}

.logout-btn {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.logout-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.main-content {
  display: grid;
  grid-template-columns: 300px 1fr 400px;
  gap: 1rem;
  padding: 1rem;
  height: calc(100vh - 80px);
}

.sidebar {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.user-info {
  text-align: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #eee;
}

.user-info h3 {
  margin: 0 0 0.5rem 0;
  color: #333;
  font-size: 1.2rem;
}

.status {
  margin: 0;
  font-size: 0.9rem;
  color: #666;
}

.status-connected { color: #28a745; }
.status-connecting { color: #ffc107; }
.status-disconnected { color: #dc3545; }

.tab-navigation {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.tab-btn {
  background: #f8f9fa;
  border: 1px solid #dee2e6;
  color: #495057;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  font-size: 0.9rem;
}

.tab-btn:hover {
  background: #e9ecef;
  transform: translateX(4px);
}

.tab-btn.active {
  background: #007bff;
  color: white;
  border-color: #007bff;
}

.content-area {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  overflow-y: auto;
}

.tab-content {
  height: 100%;
}

/* Remote Access Tab */
.remote-access-tab {
  height: 100%;
}

.access-controls {
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
}

.target-input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.target-input:focus {
  outline: none;
  border-color: #007bff;
}

.button-group {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.request-btn, .share-btn, .stop-btn, .stop-control-btn {
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.request-btn {
  background: #28a745;
  color: white;
}

.request-btn:hover {
  background: #218838;
  transform: translateY(-2px);
}

.share-btn {
  background: #007bff;
  color: white;
}

.share-btn:hover {
  background: #0056b3;
  transform: translateY(-2px);
}

.stop-btn {
  background: #dc3545;
  color: white;
}

.stop-btn:hover {
  background: #c82333;
  transform: translateY(-2px);
}

.stop-control-btn {
  background: #6c757d;
  color: white;
}

.stop-control-btn:hover {
  background: #5a6268;
  transform: translateY(-2px);
}

.access-requests {
  margin-top: 2rem;
}

.access-requests h3 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1.1rem;
}

.no-requests {
  color: #6c757d;
  font-style: italic;
  text-align: center;
  padding: 2rem;
}

.access-request {
  background: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.request-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.timestamp {
  font-size: 0.8rem;
  color: #6c757d;
}

.request-message {
  margin: 0 0 1rem 0;
  color: #495057;
}

.request-actions {
  display: flex;
  gap: 0.5rem;
}

.accept-btn, .decline-btn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.3s ease;
}

.accept-btn {
  background: #28a745;
  color: white;
}

.accept-btn:hover {
  background: #218838;
}

.decline-btn {
  background: #dc3545;
  color: white;
}

.decline-btn:hover {
  background: #c82333;
}

/* Chat Tab */
.chat-tab {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 1rem;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #dee2e6;
}

.no-messages {
  color: #6c757d;
  font-style: italic;
  text-align: center;
  padding: 2rem;
}

.chat-message {
  margin-bottom: 1rem;
  padding: 0.75rem;
  border-radius: 8px;
  max-width: 80%;
}

.chat-message.own {
  background: #007bff;
  color: white;
  margin-left: auto;
}

.chat-message.other {
  background: #e9ecef;
  color: #333;
}

.message-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
  font-size: 0.8rem;
}

.message-text {
  margin: 0;
  word-wrap: break-word;
}

.chat-input {
  display: flex;
  gap: 0.5rem;
}

.chat-input input {
  flex: 1;
  padding: 0.75rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
}

.chat-input input:focus {
  outline: none;
  border-color: #007bff;
}

.chat-input button {
  padding: 0.75rem 1rem;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.chat-input button:hover:not(:disabled) {
  background: #0056b3;
}

.chat-input button:disabled {
  background: #6c757d;
  cursor: not-allowed;
}

/* File Transfer Tab */
.file-transfer-tab {
  height: 100%;
}

.file-upload {
  text-align: center;
  padding: 2rem;
}

.file-upload h3 {
  margin: 0 0 1rem 0;
  color: #333;
}

.upload-btn {
  background: #007bff;
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.upload-btn:hover:not(:disabled) {
  background: #0056b3;
  transform: translateY(-2px);
}

.upload-btn:disabled {
  background: #6c757d;
  cursor: not-allowed;
}

.upload-hint {
  margin: 1rem 0 0 0;
  color: #6c757d;
  font-size: 0.9rem;
}

.transfer-progress {
  margin-top: 2rem;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: #e9ecef;
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: #28a745;
  transition: width 0.3s ease;
}

/* Video Panel */
.video-panel {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.video-section {
  text-align: center;
}

.video-section h3 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1rem;
}

.local-video, .remote-video {
  width: 100%;
  max-width: 100%;
  height: 150px;
  border-radius: 8px;
  background: #000;
  object-fit: cover;
}

/* Notifications */
.notification-overlay {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  animation: slideIn 0.3s ease;
}

.notification-card {
  background: white;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 1rem;
  min-width: 300px;
}

.notification-card.info {
  border-left: 4px solid #17a2b8;
}

.notification-card.success {
  border-left: 4px solid #28a745;
}

.notification-card.warning {
  border-left: 4px solid #ffc107;
}

.notification-card.error {
  border-left: 4px solid #dc3545;
}

.notification-card p {
  margin: 0;
  flex: 1;
}

.close-btn {
  background: none;
  border: none;
  color: #6c757d;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background 0.3s ease;
}

.close-btn:hover {
  background: #f8f9fa;
}

/* Animations */
@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .main-content {
    grid-template-columns: 250px 1fr 350px;
  }
}

@media (max-width: 992px) {
  .main-content {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
  }
  
  .sidebar, .content-area, .video-panel {
    order: 1;
  }
  
  .video-panel {
    order: 2;
  }
  
  .content-area {
    order: 3;
  }
}

@media (max-width: 768px) {
  .app-header {
    padding: 1rem;
    flex-direction: column;
    gap: 1rem;
  }
  
  .app-header h2 {
    font-size: 1.2rem;
  }
  
  .main-content {
    padding: 0.5rem;
  }
  
  .button-group {
    flex-direction: column;
  }
  
  .request-actions {
    flex-direction: column;
  }
}
